import React from 'react';
import { useStore } from '@/store';
import { ErrorBoundary } from '@/components/common/ErrorBoundary';
import { OfflineIndicator } from '@/components/common/OfflineIndicator';
import { Header } from '@/components/layout/Header';
import { BottomNavigation } from '@/components/layout/BottomNavigation';
import { ToastContainer } from '@/components/common/Toast';
import { CameraView } from '@/components/camera/CameraView';
import { CropView } from '@/components/crop/CropView';
import { EnhanceView } from '@/components/enhance/EnhanceView';
import { DocumentsView } from '@/components/documents/DocumentsView';
import { DocumentDetailView } from '@/components/documents/DocumentDetailView';
import { OCRView } from '@/components/ocr/OCRView';
import { ExportView } from '@/components/export/ExportView';

function App() {
  const currentView = useStore((state) => state.ui.currentView);

  const renderView = () => {
    switch (currentView) {
      case 'camera':
        return <CameraView />;
      case 'crop':
        return <CropView />;
      case 'enhance':
        return <EnhanceView />;
      case 'documents':
        return <DocumentsView />;
      case 'detail':
        return <DocumentDetailView />;
      case 'ocr':
        return <OCRView />;
      case 'export':
        return <ExportView />;
      default:
        return <CameraView />;
    }
  };

  return (
    <ErrorBoundary>
      <div className="flex flex-col h-screen overflow-hidden bg-white">
        <Header />
        <main className="flex-1 overflow-hidden mt-14 mb-16">
          {renderView()}
        </main>
        <BottomNavigation />
        <ToastContainer />
      </div>
    </ErrorBoundary>
  );
}

export default App;
